.card {
  display: flex;
  /* width: max-content; */
  flex-direction: column;
  position: relative;
  align-items: stretch;
  border: 1px  solid var(--backgroundColor);
}

.card .team-name {
  // white-space: nowrap;
  min-width: 400px;
  font-size: 18px;
  padding-top: 10px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 10px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  background: var(--backgroundColor);
  text-align: center;
}


.card .lead {
  padding: 10px;
  
  display: flex;
  flex-direction: row;
  width: auto;
  font-size: 16px;
  
  border: 0px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  background: #fff;
  text-align: left;
  
  position: relative;
  z-index: -1;
  /* left: 50%;
  transform: translateX(var(--translatex)); */
}

.card .lead .name {
  white-space: nowrap;
  flex: 1 1 0;
  text-align: left;
}

.card .lead p {
  text-align: right;
  font-size: small;
}

.card .card-body {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid rgb(218, 218, 218);
  padding-top: 0px;
  padding-left: 20px;
  padding-right: 20px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  position: relative;
  text-align: left;
  background: #fff;

}

.card .person {
  display: flex;
  flex-direction: row;
  padding: 5px;
  
  border-top: 1px solid rgb(176, 176, 176);
}

.card .person .name {
  white-space: nowrap;
  flex: 1 1 0;
  text-align: left;
}

.card .person p {
  flex: 1 1 0;
  line-height: 20px;
  font-size: small;
  text-align: right;
}

.card .card-body h2 {
  font-weight: 600;
  line-height: 20px;
}

.card .card-footer {
  padding: 3px;
  background: var(--backgroundColor);
  text-align: center;
  bottom: 0;
  position: relative;
  /* width: var(--width); */
  border-radius: 0px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  z-index: -1;
  left: 50%;
  transform: translateX(var(--translatex));
}
.card .card-footer img {
  width: 25px;
  margin: 1px;
}
